<script>
import mensagem from '../../../components/forms/mensagem.vue';
import { required,  maxLength, } from "vuelidate/lib/validators";
import { GrupoService, SubGrupoService, CategoriaService } from '../../../services'

const grupoService = new GrupoService();
const subGrupoService = new SubGrupoService();
const categoriaService = new CategoriaService();

export default {
  components: { mensagem },

    data() {
        return {
            dados: [],
            // dadosFiltrados: [],
            filtro:{
                ordenar: 'descricao',
                ordem: 'asc',
                page: 1,
                per_page: 2000,
                texto: '',
            },
            paginacao: {
                current_page: 0,
                // total: 0,
                last_page: 0,
                from: 0,
                to: 0,
                per_page: 2000
            },
            debounce: null,
            grupo: null,
            sucessoDeletar: null,
            erroDeletar: null,
            sucessoEditar: null,
            erroEditar: null,

            subGrupoModal: null,
            sucessoSubGrupo: null,
            erroSubGrupo: null,
            sucessoSubGrupo: null,
            erroSubGrupo: null,

            categoriaModal: null,
            sucessoCategoria: null,
            erroCategoria: null,
            sucessoCategoria: null,
            erroCategoria: null,
            
            ver_todos: false

        };
    },
    validations() {
        return {
            grupo: {
                descricao: { required, maxLength:maxLength(50)},
            },
            subGrupoModal: {
                descricao: { required, maxLength:maxLength(50)},
            },
            categoriaModal: {
                descricao: { required, maxLength:maxLength(50)},
            },
        }
    },
    mounted() {
        this.buscar();
    },
    methods: {

        novoItem: function(obj){
            this.sucessoEditar = null;
            this.erroEditar = null;
            this.$v.grupo.$reset();
            this.grupo = {
                descricao: ''
            };
            
        },

        abrirModalEditar:function(obj){
            this.sucessoEditar = null;
            this.erroEditar = null;
            this.$v.grupo.$reset();
            this.grupo = {...obj};
        },

        salvar: function(){
            this.sucessoEditar = null;
            this.erroEditar = null;
            this.$v.grupo.$touch();
            if(this.$v.grupo.$invalid){
                return false;
            }            

            let req = this.grupo.id ? grupoService.atualizar(this.grupo) : grupoService.criar(this.grupo);

            req.then( response => {
                let obj = response.data;
                let index = this.dados.map(x => x.id).indexOf(obj.id);
                if(index >= 0){
                    this.dados.splice(index, 1, obj);
                }else{
                    this.dados.push(obj);
                }
                    
                this.sucessoEditar = 'Grupo salvo com sucesso!';
            }).catch( erro => this.erroEditar = erro)            
        },

        abrirModalDeletar: function(obj){
            this.sucessoDeletar = null;
            this.erroDeletar = null;
            this.grupo = {...obj};
        },

        deletar: function(){
            this.sucessoDeletar = null;
            this.erroDeletar = null;

             grupoService.deletar(this.grupo.id)
                .then( response => {
                    let i = this.dados.map(x => x.id).indexOf(this.grupo.id);
                    this.dados[i].ativo = false;
                    this.sucessoDeletar = 'Registro removido com sucesso!';
                })
                .catch( erro => this.erroDeletar = erro)            
        },

        debounceBusca: function(){
            clearTimeout(this.debounce);
            this.debounce = setTimeout(() => {
                this.navegar(1);
            }, 800);
        },

        
        buscar:function(){
            // if(this.dados.length > 0)
            //     return this.filtrar();
            grupoService.listar(this.filtro).then(
                    response => {
                        this.$data.dados = response.data.data.map(x => {
                            x.ver = false;
                            x.sub_grupos = x.sub_grupos.map( s => {
                                s.ver = false;
                                return s;
                            });
                            return x;
                        });
                        
                        this.$data.paginacao = response.data;
                        this.$data.filtro.page = response.data.current_page;
                        this.$data.paginacao.per_page = response.data.per_page;
                    }
                )
        },

        navegar: function(pagina){
            this.filtro.page = pagina;
            this.buscar();
        },


        /**Area do Sub Grupo */

        novoSubGrupo: function(obj){
            this.sucessoSubGrupo = null;
            this.erroSubGrupo = null;
            this.$v.subGrupoModal.$reset();
            this.grupo = {...obj};
            this.subGrupoModal = {
                grupo_id: obj.id,
                descricao: ''
            };
            
        },

        salvarSubGrupo: function(){
            this.sucessoSubGrupo = null;
            this.erroSubGrupo = null;
            this.$v.subGrupoModal.$touch();
            if(this.$v.subGrupoModal.$invalid){
                return false;
            }
        
            let req = this.subGrupoModal.id ? subGrupoService.atualizar(this.subGrupoModal) : subGrupoService.criar(this.subGrupoModal);
    
            req.then( response => {
                let obj = response.data;
                let grupo = this.dados[this.dados.map(x => x.id).indexOf(obj.grupo_id)];
                let index = grupo.sub_grupos.map(x => x.id).indexOf(obj.id);
                if(index >= 0){
                    grupo.sub_grupos.splice(index, 1, obj);
                }else{
                    grupo.sub_grupos.push(obj);
                }

                this.subGrupoModal = {...obj};
                    
                this.sucessoSubGrupo = 'Sub-Grupo salvo com sucesso!';
            }).catch( erro => this.erroSubGrupo = erro)            
        },

        abrirModalEditarSubGrupo: function(grupo, subgrupo){
            this.sucessoSubGrupo = null;
            this.erroSubGrupo = null;
            this.$v.subGrupoModal.$reset();
            this.grupo = {...grupo};
            this.subGrupoModal = {...subgrupo};
        },


        /**Area da Categoria */
        novaCategoria: function(grupo,subgrupo){
            this.sucessoCategoria = null;
            this.erroCategoria = null;
            this.$v.categoriaModal.$reset();
            this.subGrupoModal = {...subgrupo};
            this.grupo = {...grupo};
            this.categoriaModal = {
                sub_grupo_id: subgrupo.id,
                descricao: ''
            };            
        },

        salvarCategoria: function(){
            this.sucessoCategoria = null;
            this.erroCategoria = null;
            this.$v.categoriaModal.$touch();
            if(this.$v.categoriaModal.$invalid){
                return false;
            }
            let req = this.categoriaModal.id ? categoriaService.atualizar(this.categoriaModal) : categoriaService.criar(this.categoriaModal);
    
            req.then( response => {
                let obj = response.data;
                let grupo = this.dados[this.dados.map(x => x.id).indexOf(this.grupo.id)];
                let sub_grupo = grupo.sub_grupos[grupo.sub_grupos.map(x => x.id).indexOf(obj.sub_grupo_id)];
                let index = sub_grupo.categorias.map(x => x.id).indexOf(obj.id);
                if(index >= 0){
                    sub_grupo.categorias.splice(index, 1, obj);
                }else{
                    sub_grupo.categorias.push(obj);
                }

                this.categoriaModal = {...obj};
                    
                this.sucessoCategoria = 'Sub-Grupo salvo com sucesso!';
            }).catch( erro => this.erroCategoria = erro)            
        },

        abrirModalEditarcategoria: function(grupo, subgrupo, categoria){
            this.sucessoCategoria = null;
            this.erroCategoria = null;
            this.$v.categoriaModal.$reset();
            this.grupo = {...grupo};
            this.subGrupoModal = {...subgrupo};
            this.categoriaModal = {...categoria};
        },

        alternarTodos: function(){
            this.ver_todos = !this.ver_todos;
            this.dados = this.dados.map(x => {
                x.ver = this.ver_todos;
                x.sub_grupos = x.sub_grupos.map( s => {
                    s.ver = this.ver_todos;
                    return s;
                });
                return x;
            });
        }
  
    }
};
</script>
